        </div>
    {/if}

    {#if editcoord && coordinates }
        <div class="editcoord" use:clickOutside on:click_outside={handleClickOutside}>
            <div class="formLine">

                <div class="formLine svelte-135lqg4">
                    <label for="x" class="more-label svelte-135lqg4">x:</label>
                    <input  value={coordinates.x} on:change={(e) => {console.log("change text ",e.target.value); coordinates.x=e.target.value;coordinates=coordinates;change("x",e.target.value) }} type="text" class="formInput svelte-135lqg4" placeholder="" style="font-size: 12px; width: 220px; display: inline-block;">
                </div>

                <div class="formLine svelte-135lqg4">
