                </div>

                <div class="formLine svelte-135lqg4">
                    <label for="width" class="more-label svelte-135lqg4">width:</label>
                    <input value={coordinates.width} on:change={(e) => {console.log("change text ",e.target.value); coordinates.width=e.target.value;coordinates=coordinates;change("width",e.target.value) }} type="text" class="formInput svelte-135lqg4" placeholder="" style="font-size: 12px; width: 220px; display: inline-block;">
                </div>

                <div class="formLine svelte-135lqg4">
                    <label for="height" class="more-label svelte-135lqg4">height:</label>
                    <input value={coordinates.height} on:change={(e) => {console.log("change text ",e.target.value); coordinates.height=e.target.value;coordinates=coordinates;change("height",e.target.value) }} type="text" class="formInput svelte-135lqg4" placeholder="" style="font-size: 12px; width: 220px; display: inline-block;">
                </div>


                <div class="formLine svelte-135lqg4">
                    <label for="rotation" class="more-label svelte-135lqg4">rotation:</label>
                    <input value={(coordinates.rotation * 180.0) / Math.PI} on:change={(e) => {console.log("change text ",e.target.value); coordinates.rotation=e.target.value;coordinates=coordinates;change("rotation", parseInt(e.target.value) * (Math.PI/180)) }} type="text" class="formInput svelte-135lqg4" placeholder="" style="font-size: 12px; width: 220px; display: inline-block;">
                </div>
            </div>
        </div>
    {/if}
</div>

